<template>
	<div class="login" @keyup.enter="login">
    <el-form class="login-form">
      <h1 class="title">样板管理系统</h1>
      <el-form-item>
        <el-input
          :autofocus="true"
          placeholder="请输入账号"
          v-model="username">
          <template slot="prepend"><i class="el-icon-mobile-phone"></i></template>
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-input
          placeholder="请输入密码"
          type="password"
          v-model="password">
          <template slot="prepend"><i class="el-icon-info"></i></template>
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button style="width:100%" @click.native="login" type="primary" :loading="isBtnLoading">{{btnText}}</el-button>
      </el-form-item>
    </el-form>
    <a class="reg" @click="toReg"> 跳转注册页面 </a>
  </div>
</template>

<script>
  import api from 'api/user'
  import * as util from 'assets/js/util'
  
  export default {
    name: "",
    data() {
      return {
        username: '',
        password: '',
        isBtnLoading: false
      }
    },
    computed: {
      btnText() {
        if (this.isBtnLoading) {
          return '登录中...'
        }
        return '登录'
      }
    },
    methods: {
      toReg() {
        this.$router.push('/reg')
      },
      login() {
        if (!(this.username.trim())) {
          this.$message.error('请填写用户名')
          return
        }
        if (!this.password) {
          this.$message.error('请填写密码')
          return
        }
        let loginParams = {
          userName: this.username,
          userPassword: this.password
        }
        this.isBtnLoading = true
        var t = JSON.stringify(loginParams)
        api.Login(t).then(res => {
          console.log(res)
          this.isBtnLoading = false
          if (res.data.success === 'SUCCESS') {
            this.$message.success("登录成功")
            this.username = ''
            this.password = ''
            let obj = {
              token: res.data.data
            }
            util.handleSession('token', obj)
            this.$emit('login', this.$router.currentRoute.query.from)
          } else {
            this.$message.error("登录失败，用户名或密码错误")
          }
        }).catch(err => {
          this.$message.error("登录失败，服务器异常")
          console.log(err)
        })
      }
    },
    created() {
      sessionStorage.clear()
    }
  }
</script>

<style scoped>
  .login-form {
    width: 400px;
    margin: 13% auto 0;
  }
  .title {
    margin-bottom: 30px
  }
  @media screen and (max-width: 767px) {
    .login-form {
      width: 80%;
      margin: 20% auto 0;
    }
  }
</style>



